/* General reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Chat container */
  .chat-container {
    position: absolute;
    bottom: 20px;
    right: 20px;
    width: 350px;
    max-height: 500px;
    background-color: rgba(255, 255, 255, 0.9); /* Slight transparency for visibility */
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
  }
  
  /* Chat header */
  .chat-header {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  
  /* Messages container */
  .messages-container {
    flex-grow: 1;
    overflow-y: auto;
    padding: 10px;
  }
  
  /* Input container */
  .input-container {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  
  /* Scrollbar styling (optional for a cleaner look) */
  .messages-container::-webkit-scrollbar {
    width: 5px;
  }
  
  .messages-container::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
    font-family: 'Helvetica Neue', sans-serif;
    background-color: #ffffff;
    color: #333;
}

body {
    display: flex;
    flex-direction: column;
}

.navbar {
    background-color: white;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
}

.logo {
    font-size: 32px;
    font-weight: bold;
}

nav ul {
    list-style-type: none;
    display: flex;
}

nav ul li {
    margin-left: 30px;
}

nav ul li a {
    text-decoration: none;
    color: #333;
    font-size: 16px;
    font-weight: 600;
}

/* Hide menu icon by default and make it visible for mobile */
.menu-icon {
    display: none;
    font-size: 30px;
    cursor: pointer;
}

/* Info bar styling */
.info-bar {
    background-color: #f7f7f7;
    text-align: center;
    padding: 10px;
    font-size: 14px;
    font-weight: 400;
    border-bottom: 1px solid #ddd;
}

.info-bar span {
    margin: 0 15px;
}

main {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
    background-color: white;
}

.landing {
    text-align: center;
    width: 100%;
}

.banner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px 0;
}

.banner-text {
    text-align: left;
    max-width: 40%;
}

.banner-text h1 {
    font-size: 56px;
    color: #000;
    margin-bottom: 20px;
}

.banner-text p {
    font-size: 18px;
    color: #666;
    margin-bottom: 20px;
}

.cta-button {
    padding: 10px 20px;
    background-color: #333;
    color: white;
    border: none;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.cta-button:hover {
    background-color: #555;
}

.image-placeholder img {
    max-width: 100%;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.category-grid {
    display: flex;
    justify-content: space-around;
    padding: 40px 0;
}

.category-item {
    width: 30%;
    text-align: center;
}

.category-item img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.category-item span {
    display: block;
    margin-top: 10px;
    font-size: 18px;
    font-weight: bold;
    color: #333;
}

/* Media Queries for responsive design */
@media (max-width: 768px) {
    .navbar nav ul {
        display: none; /* Hide menu on smaller screens */
    }

    .menu-icon {
        display: block; /* Show menu icon on mobile */
    }

    .nav-menu.visible {
        display: flex;
        flex-direction: column;
        position: absolute;
        background-color: white;
        top: 70px;
        left: 0;
        width: 100%;
        padding: 20px;
    }

    .banner {
        flex-direction: column;
        text-align: center;
    }

    .banner-text {
        max-width: 100%;
    }

    .banner-text h1 {
        font-size: 32px;
    }

    .banner-text p {
        font-size: 16px;
    }

    .category-grid {
        flex-direction: column;
    }

    .category-item {
        width: 100%;
        margin-bottom: 20px;
    }

    .cta-button {
        font-size: 14px;
    }
}

