/* Overlay to dull the page */
.page-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4); /* Darkens the page */
  z-index: 1; /* Ensure it sits above all page content but below the chat widget */
}

/* Chat container (above overlay) */
.chat-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 350px;
  max-height: 80vh;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
