}


/* new */
/* Chat widget base styles */
.chat-widget {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}

@keyframes Bob {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 100px;
  }
}

.chat-icon {
  animation: Bob 1s linear infinite;
  background-color: white;
  color: white;
  padding: 5px;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}


.chat-icon:hover {
  transform: scale(1.1);
}

/* Chat container (speech bubble) */
.chat-container {
  position: absolute;
  bottom: 70px;
  right: 0;
  width: 350px;
  max-height: 80vh;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transform: scale(0);
  transform-origin: bottom right;
  animation: bubbleIn 0.3s forwards;
}

/* Speech bubble arrow */
.chat-container::before {
  content: '';
  position: absolute;
  bottom: -10px;
  right: 15px;
  border-width: 10px;
  border-style: solid;
  border-color: white transparent transparent transparent;
}

/* Bubble animation */
@keyframes bubbleIn {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

